*, *::before, *::after{
    box-sizing: border-box;
    font-family: Helvetica, sans-serif;
}

:root{
    --add-food-item-cell-size: 45px;
    --delete-food-item-cell-size: 30px;
    --mark-size: calc(var(--add-food-item-cell-size) * .9);
    --input-font-size: 1.15rem
}

