.hide{
    display: none;
}

body{

}

h1{
    text-align: center;
}

h1, h3, label, button, input{
    font-style: helvetica;
}

h3{
    font-size: calc(var(--input-font-size) * 1.2);
}

input[type='text']{
    padding: 4px;
    padding-left: 8px;
    width: 85%;
    margin-right: 5px;
    margin-bottom: 3px;
    font-size: var(--input-font-size);
    font-weight: bold;
    border: 1px solid rgba(0, 0, 0, 0.4);
    outline: none;
    border-radius: 10px;
}

input[type='text']:focus{
    border-color: transparent;
    box-shadow: 0 0 1px 2px rgb(228, 148, 0);
}

.intermeal-checkbox input {
    margin: 0;
    width: auto;
    -ms-transform: translate(0%, 20%);
    transform: translate(0%, 20%);
}       

label{
    font-size: calc(var(--input-font-size) * 0.7);
    color: grey;
}

.output{
    font-size: 1.1rem;
    font-weight: bold;
}

.calculateIE-button, 
.save-data-button,
.load-data-button{
    font-size: calc(var(--input-font-size) * 1.2);
    padding: 10px;
    border: 1px solid black;
    border-radius: 3px;
    position: relative;
    -ms-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
    margin-right: 10px;
}

.calculateIE-button:active,
.save-data-button:active,
.load-data-button:active{
    background-color: rgb(211, 210, 210);
}

.load-data-button{
    -ms-transform: translate(0%, 0%);
    transform: translate(0%, 0%);
}

.save-data-button{
    float: left;
}

.calculateIE-button{
    
}

.delete-food-item-button{
    margin: 0;
    float: right;
    position: relative;
    width: var(--delete-food-item-cell-size);
    height: var(--delete-food-item-cell-size);
    background-color: transparent;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.1rem;
}


.delete-food-item-button::before,
.delete-food-item-button::after{
    content:"";
    background-color: black;
    position: absolute;
    width: calc(var(--delete-food-item-cell-size) * 0.09);
    height: calc(var(--delete-food-item-cell-size) * 0.65);
}

.delete-food-item-button::before{
    transform: rotate(45deg);
}

.delete-food-item-button::after{
    transform: rotate(-45deg);
}

.add-new-food-item-button{
    margin-top: calc(0.6 * var(--add-food-item-cell-size));
    position: relative;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: var(--add-food-item-cell-size);
    height: var(--add-food-item-cell-size);
    border-radius: 50%;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}

button:focus{
    outline: none
}

.add-new-food-item-button::before, 
.add-new-food-item-button::after{
    content:"";
    background-color: black;
    position: absolute;
    width: calc(var(--mark-size) * 0.095);
    height: calc(var(--mark-size) * 0.8);
}

.add-new-food-item-button::after{
    transform: rotate(90deg);
}

